import * as React from 'react';
import { ArtPiecePreview } from './ArtPiecePreview';
import { ArtPiecesState } from '../Models/ArtPiecesState';
import { ModelArtPiecePreview } from '../Models/ModelArtPieceDisplay';
import axios from 'axios';

export class ArtPieceDisplay extends React.Component<{}, ArtPiecesState>{
    
    constructor(props: {}) {
        super(props);
    
        // init state
        this.state = {
          items: [],
          firstPage: true,
          lastPage: false,
          pageNumber: 0
        };

    }

    async GetArtPieces(indexAdjustment: number){
        this.setState({
            pageNumber: this.state.pageNumber + indexAdjustment,
            firstPage: (this.state.pageNumber - indexAdjustment) === 0
        });

        const query = `
            query getArtPieces($pageNumber: Int!) {
                ArtPieces(pageNumber: $pageNumber) {
                    title
                    artistName
                    imageURL
                }
            }
          `;

        // These variables are optional, leave empty for now
        const variables = {
            pageNumber: this.state.pageNumber
        };


        const response = await axios.post('http://localhost:8082/api', {
            query,
            variables
        });

        var newItems: ModelArtPiecePreview[] = response.data.data.ArtPieces;
        

        this.setState({items: newItems})
    }
    
    componentDidMount(){
        
        this.GetArtPieces(0);
    }

    render(){
        return <div className="ArtPieceDisplayPanel container">
            <h2 className="pageTitle">Prints</h2>
            <div className="row">
                {this.state.items.map((item,index)=>{
                    return <div className="col-sm-4" key={index}>
                            <ArtPiecePreview imageURL={item.imageURL} title={item.title} artistName={item.artistName}/>
                        </div>
                })}
            </div>
            <div className="buttonContainer">
                {!this.state.firstPage ? <button id="PreviousButton" className="button" onClick={(clickEvent) => {this.GetArtPieces(-10)}}>Previous</button>: <></>}
                {!this.state.lastPage ? <button id="NextButton" className="button"  onClick={(clickEvent) => {this.GetArtPieces(10)}}>Next</button> : <></>}
            </div>
        </div>
    }
}